import axios from "axios";

export default {
  name: "ChatBox",
  data: () => ({
    personName: null,
    messages: [{ text: "Hola, ¿como te llamas?", author: "bot", type: "left" }],
    dialogs: {
      bot: [
        "Encantado de conocerte ",
        "Yo soy Pastelito-Bot",
        "Por cierto, ¿tienes hambre?"
      ],
      human: [],
      step: 0
    },
    userSelect: false,
    time: null
  }),

  methods: {
    sendName() {
      this.messages.push({
        text: `Me llamo ${this.personName}`,
        author: "human",
        type: "right"
      });

      this.time = setInterval(() => {
        this.nextDialog();

        if (this.dialogs.step == 3) {
          this.stopStep();
          this.userSelect = true;
        }
      }, 2000);
    },

    stopStep() {
      clearInterval(this.time);
    },

    userSelectYes() {
      this.userSelect = false;
      this.messages.push({
        text: "Si, ¡me muero de hambre!",
        author: "human",
        type: "right"
      });
      setTimeout(() => {
        axios
          .get(
            "https://xz94zfs6u8.execute-api.eu-west-1.amazonaws.com/default/myBakery"
          )
          .then(res => {
            let foodDay =  res.data
            this.messages.push({
              text: `¡Seguro que te gusta un buen ${foodDay}!`,
              author: "bot",
              type: "left"
            });
          })
          .catch(err => {
            console.error("Error en coger de la API la comida", err);
            this.userSelect = true;
            this.messages.push({
              text: '¡Ups! parece que tengo un error en mis circuitos, espera un rato hasta que lo arregle',
              author: "bot",
              type: "left"
            });
          });
      }, 2000);
    },

    userSelectNo() {
      this.messages.push({
        text: "No tengo mucha hambre",
        author: "human",
        type: "right"
      });
      setTimeout(() => {
        this.messages.push({
          text:
            "¿Seguro que no?, ¡tengo unas sugerencias que te haran la boca agua!",
          author: "bot",
          type: "left"
        });
      }, 2000);

      this.userSelect = true;
    },

    nextDialog() {
      if (this.dialogs.step == 0) {
        this.messages.push({
          text: this.dialogs.bot[this.dialogs.step] + this.personName,
          author: "bot",
          type: "left"
        });
        this.dialogs.step++;
      } else {
        this.messages.push({
          text: this.dialogs.bot[this.dialogs.step],
          author: "bot",
          type: "left"
        });
        this.dialogs.step++;
      }
    }
  }
};
